section.page-section {
  position: relative;
  overflow: hidden;

  h2 {
    margin-bottom: 20px;
  }

  @include big {
    height: 100%;
    
    > .inner {
      display: table;
      width: 100%;
      height: 100%;

      > .content {
        display: table-cell;
        vertical-align: middle;
      }
    }
  }
}

section.page-multi-section {
  @include big {
    position: relative;
    height: 300%;

    > section.page-section {
      height: (100% / 3);
      width: (100% * 2 / 3);

      > .inner > .content {
        padding-left: 100px;
        padding-right: 100px;

        @media (max-width: $break-wide-max) {
          padding-left: 50px;
          padding-right: 50px;
        }
      }
    }
  }

  ~ section.page-section {
    z-index: 102;
    transform: translateZ(0);
  }
}

section.intro-section {
  z-index: 101;

  > .inner {
    padding-top: 200px;
    padding-bottom: 200px;
  }

  h1 {
    margin-bottom: 50px;
  }

  h4 {
    margin-bottom: 20px;
  }
}

section.feed-section{
  .inner{
    .content{
      .did-you-know{
        margin:60px auto 0;
        max-width:400px;

        h4{
          margin: 0 0 5px;
        }

        .cycle-shuffle-button{
          border:none;
          background-color:white;
          font-size:20px;
          color:$honey-teal;
          margin:10px 0 0;
          transition:.5s ease;
          cursor:pointer;

          &:hover{
            transform:rotate(90deg);
          }
         
          &:focus{
            outline:none;
          }
        }
      }
    }
  }
}

section.features-section {
  h2 {
    @include big {
      position: absolute;
      top: 40px;
      left: 0;
      right: 0;
    }
  }

  .features-content {
    max-width: 1100px;
    margin: auto;
    overflow: hidden;

    &:after {
      display: none;
    }

    @include small {
      &:after {
        content: 'flickity';
      }
    }

    h3{
      margin-bottom: 0.5em;
    }

    p {
      @extend p.small;
    }

    .features-content-item {
      @include big {
        float: left;
        clear: left;
        text-align: right;
        padding-left: 20px;
        padding-right: 20px;
        width: (100% / 3);
        cursor: pointer;
        opacity: .3;
        margin: 15px 0;

        &.active {
          opacity: 1;
        }

        &:nth-child(odd) {
          float: right;
          clear: right;
          text-align: left;
        }
      }

      @include small {
        width: 100%;
      }
    }
  }

  .features-phone {
    position: relative;
    background-image: url('../assets/images/iphone-frame.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    width: 180px;
    height: 370px;
    margin: auto;

    @include big {
      @include fill;
      width: (100% / 3);
      max-width: 250px;
      height: auto;
    }

    .features-phone-screenshot {
      display: none;

      &.active {
        display: block;
      }

      img {
        @include fill;
        bottom: 2.3%;
        left: 0.25%;
        max-width: 82.5%;
        margin: auto;
      }
    }
  }
}

section.about-section{
  .content{
    padding:25px 0;

    @include small{
      padding:100px 25px;
    }

    h1, p{
      max-width:825px;
    }

    h1{
      margin:0 auto 25px;
    }

    .founders{
      max-width:600px;
      margin:50px auto 75px;
      overflow:auto;

      .half{
        width:50%;

        img{
          margin-bottom:15px;
        }

        h4{
          .cred{
            display:inline-block;
            color:$honey-teal;
          }
        }

        &.michal{
          float:left;
        }

        &.rebecca{
          float:right;
        }
      }

      @include small{
        margin: 0 auto;

        .half{
          width:100%;
          margin: 35px 0;

          &.michal, &.rebecca{
            float:none;
          }
        }
      } 
    }

    .pledge{
      max-width:1140px;
      margin: 0 auto;

      .third{
        width:(100% / 3);
        display:inline-block;
        float:left;

        h3{
          margin:0 0 15px;
        }

        h5{
          width:90%;
        }
      }

      @include small{
        .third{
          width:100%;
          margin: 20px 0;
          float:none;
        }
      } 
    }
  }
}

footer.bottom {
  position: relative;
  min-height: 300px;
  z-index: 500;
  transform: translateZ(0);
  padding: 50px 25px;

  h5{
    color:white;
    max-width:1025px;
    font-weight:500;
    margin:25px auto 50px;

    &.copyright{
      opacity:.5;
      margin: 15px auto 0;
    }
  }

  .social{
    a{
      text-decoration:none;
      color:white;
      font-size:25px;
      margin:0 10px;
      transition:.25s ease;
      transform:scale(.85);

      &:hover{
        // opacity:.5;
        transform:scale(1);
      }
    }
  }
}

.bg-overlay {
  position: relative;
  background-size: cover;
  background-position: 50% 50%;
  transform: translateZ(0);

  &:before, &:after {
    content: '';
    position: absolute;
    top: 0; bottom: 0;
    left: 0; right: 0;
  }

  &:before {
    background: $honey-overlay;
    z-index: -1;
  }

  &:after {
    background: inherit;
    opacity: 0;
    z-index: 1;
    transition: opacity 2000ms;
  }

  > .inner {
    position: relative;
    z-index: 2;
  }

  &.bg-loaded {
    &:after {
      opacity: .14;
    }
  }

  h1 {
    color: white;
  }

  h4 {
    color: $honey-orange;
  }
}

.bg-white {
  background: white;

  h1, h2, h4 {
    color: $honey-orange;
  }

  h3{
    color:$honey-teal;
  }
}

.bg-orange {
  background: $honey-orange;
  background: linear-gradient(to right, #FDB73B, #F59121);
}